const assert = require('assert');

const assertParam = require('../util/assertParam');
const RestUtil = require('../util/RestUtil');
const buildCommonQuery = require('../util/buildCommonQueryParams');

/*
  https://demo.microstrategy.com/MicroStrategyLibrary/api-docs/index.html#/Browsing
*/
module.exports = class RestTopic extends RestUtil {

  /**
   * @private get base endpoint for this API category
   * @returns {string} endpoint prefix
   */
  _getBase() {
    return 'folders';
  }

  /**
   * @public Get contents of a folder
   *
   * @param {string} folderId - folder to browse
   * @param {number} [offset=0] starting page
   * @param {number} [limit=-1] max items per request
   * @param {array} [fields]
   * @returns {object} representing folder definition
   */
  getFolder(folderId, offset = 0, limit = -1, fields) {
    assert(folderId, 'No folderId provided');

    const queryParameter = buildCommonQuery(fields, offset, limit);
    return this._makeRequest(`${this._getBase()}/${folderId}`, queryParameter, 'GET', this.getProjectHeader())
      .then(result => this.throwIfFailed(result, 200))
      .catch(e => this.throwExceptionResponse(e));
  }

  /**
   * @public Get list of folders
   *
   * @param {string|boolean} [inProjectId=false] if provided or truthy, folders in project
   * @param {number} [offset=0] starting page
   * @param {number} [limit=-1] max items per request
   * @param {array} [fields]
   * @returns {array} list of folders
   */
  getFolders(inProjectId, offset = 0, limit = -1, fields) {
    const queryParameter = buildCommonQuery(fields, offset, limit);
    const projectHeaders = inProjectId && this.getProjectHeader(inProjectId);

    return this._makeRequest(this._getBase(), queryParameter, 'GET', projectHeaders)
      .then(result => this.throwIfFailed(result, 200))
      .catch(e => this.throwExceptionResponse(e));
  }

  /**
   * @public Get contents of pre-predefined folder in current project.
   *
   * @param {number} folderType - EnumDSSXMLFolderNames = https://lw.microstrategy.com/msdz/MSDL/GARelease_Current/docs/ReferenceFiles/reference/com/microstrategy/webapi/EnumDSSXMLFolderNames.html
   * @param {number} [offset=0]
   * @param {number} [limit=-1]
   * @param {array} fields
   * @returns {array} folder contents
  */
  getPredefinedFolder(folderType, offset = 0, limit = -1, fields) {
    const queryParameter = buildCommonQuery(fields, offset, limit);
    return this._makeRequest(this._getBase() + '/preDefined/' + folderType, queryParameter, 'GET', this.getProjectHeader())
      .then(result => this.throwIfFailed(result, 200))
      .catch(e => this.throwExceptionResponse(e));
  }

  /**
   *
   *
   * @param {*} fields
   */
  getPersonalObjectsFolder(fields) {

  }


  /**
   * @public Publish a specific multi-table dataset using data uplaoded to the Intelligence Server.
   * Use after adding data to existing upload session.
   *
   * @param {string}datasetId
   * @param {string}uploadSessionId
   * @returns {Promise} - throws on failure, resolves on succses. Use getUploadSessionStatus() to poll publish status.
   */
  publishUploadSessionDataset(datasetId, uploadSessionId) {
    assert(datasetId, 'No datasetId provided');
    assert(uploadSessionId, 'No uploadSessionId provided');
    const uploadSessionBasePath = this._getUploadSessionBase(datasetId);
    const endpoint = `${uploadSessionBasePath}/${uploadSessionId}/publish`;

    return this._makeRequest(endpoint, false, 'POST', this.getProjectHeader())
      .then(result => this.throwIfFailed(result, 200))
      .catch(e => this.throwExceptionResponse(e));
  }

};